<template>
    <section class="main-content" id="tabs">
        <tg-list :showCheckBox="true" :IsShowAction="false" :listType="['List']" ModuleName="Asset" SubModuleCode="Asset" :PageSize="PageSize"
            IdentityColumn="ASSET_TYPE_ID" HeaderText="RFID Asset Listing" :ListData="RfidData" :HeaderData="Headers"
            :widgets="widgets" :callbackfunction="FetchData" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecord"
            :SearchFields="leftSearchFields" :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchData"
            :LegendArray="LegendArray" :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NorecordfoundText')"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:ActionButtonClick="actionButtonClick"
            v-on:SortdataButtonClick="sortdata">
            
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'Image'">
                    <div class="form-control p-0 border-0 text-center" v-if="data.row.ATTACHMENT_PATH !=null" alt="" title="">
                        <img :src='data.row.ATTACHMENT_PATH' style="max-height:80px;" title=""/>
                    </div>
                    <div class="form-control p-0 border-0 text-center" v-if="data.row.ATTACHMENT_PATH ==null">
                        <img src='/Content/images/imagenotavailable.jpg' alt="No Image Available" style="max-height:60px;" title=""/>
                    </div>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'ASSET_TYPE'">
                    <span class="text-dark" v-if="data.row.ASSET_TYPE">
                        <a href="javascript:void(0);" v-on:click="AssetTypeEdit(data.row.ASSET_TYPE_ID)"
                            class="relatedproduct" :id="data.row.ASSET_TYPE" title="Click here to edit Product type">
                            <em class="text-primary" aria-hidden="true">{{ data.row.ASSET_TYPE }}</em>
                        </a>
                    </span>

                </template>
                <template v-if="data.column.COLUMN_NAME == 'DESCRIPTION'">
                    <span class="text-dark center-icon" :title="data.column.COLUMN_NAME">
                        <em v-if="data.row.DESCRIPTION != ''" class="text-dark center-icon">{{ data.row.DESCRIPTION }}</em>
                        <em v-else class="text-dark center-icon">--</em>
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'IsContainer'">
                    <span class="text-dark center-icon" :title="data.column.COLUMN_NAME">
                        <em v-if="data.row.IS_CONTAINER == 1" class="text-dark center-icon">Yes</em>
                        <em v-else class="text-dark center-icon">No</em>
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'STATUS_NAME'">
                    <td class="text-center single-action">
                        <span class="cstm-drop-btn">
                            <select name="Reader" v-model="data.row.STATUS_ID"
                                v-on:change="changeStatus(data.row.ASSET_TYPE_ID, data.row.STATUS_ID)"
                                :class="{ 'ddlupdatestatus w-80 btn-success': data.row.STATUS_ID == 1001, 'ddlupdatestatus w-80 btn-danger': data.row.STATUS_ID == 1002 }">

                                <option v-for="(item, itemIndex) in StatusListData" :key="itemIndex"
                                    :value="item.STATUS_ID">{{ item.STATUS }}</option>
                            </select>
                        </span>
                    </td>
                </template>
            </template>
        </tg-list>
        <div class="my-popups" v-if="Addtype">
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header">
                            <h5 class="modal-title">
                                {{ $t('ProductType') }}
                            </h5>
                            <button v-on:click="ClosePopupProductType()" type="button" class="close"
                                data-original-title="Close"><em class="fa fa-times"></em></button>
                        </div>
                        <AddProductType :FromPage="FromPage" :AseetTypeIde="AseetTypeId"></AddProductType>
                    </div>
                </div>
            </div>
        </div>

    </section>
</template>

<script>

import Schema from "../Asset/ProductTypeSchema.js"
import DataService from '../../service/DataService'
import AddProductType from '../../components/Modal/AddProductType.vue'

export default {
    components: {
        AddProductType
    },
    data() {
        return {
            leftSearchFields: '',
            listheaderbuttons: Schema.buttons,
            LegendArray: [],
            RfidData: [],
            LeadStatusList: [],
            StatusListForListing: [],
            Headers: [
                {
                    "COLUMN_NAME": "Image",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Image",
                    "DISPLAY_ORDER": 1,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "TYPE_CODE",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Product Type Code",
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "ASSET_TYPE",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Product Type Name",
                    "DISPLAY_ORDER": 3,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "DESCRIPTION",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Description",
                    "DISPLAY_ORDER": 4,
                    settings: {
                        isInSlot: true
                    }
                },
                {
                    "COLUMN_NAME": "IsContainer",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Container",
                    "DISPLAY_ORDER": 5,
                    settings: {
                        isInSlot: true
                    }
                },

                {
                    "COLUMN_NAME": "STATUS_NAME",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Status",
                    "DISPLAY_ORDER": 6,
                    settings: {
                        isInSlot: true
                    }
                },

            ],
            widgets: [],
            noRecord: true,
            TotalRecord: 0,
            CurrentPage: 1,
            TotalPage: 0,
            txtsearch: '',
            statusIds: '',
            LocationId: '',
            SortBy: '',
            SortExp: '',
            PageNumber: 1,
            isPaged: 1,
            PageSize: 10,
            ePC_TAG: '',
            searchCondition: "",
            IsProductHistory: false,
            legendProgress: [{
                class: '',
                backgroundColor: '#17a2b8',
                name: 'Draft'
            },
            {
                class: 'legend-Progress',
                backgroundColor: '',
                name: 'Approved'
            },
            {
                class: 'legend-pending',
                backgroundColor: '',
                name: 'Pending'
            },
            {
                class: '',
                backgroundColor: '#fe6847',
                name: 'SendForCorrection'
            },
            {
                class: '',
                backgroundColor: '#ee1313',
                name: 'Rejected'
            }

            ],
            privilegeParams: null,
            StatusListData: [],
            Addtype: false,
            AseetTypeId: 0,
            FromPage:''
        }
    }, 
    mounted() {
        document.addEventListener('click', this.handleDocumentClick);
    },
    beforeDestroy() {
        document.removeEventListener('click', this.handleDocumentClick);
    }, 
    async created() {
        var vm = this;
        await vm.GetPageSize();
        vm.privilegeParams = `controller=Asset&action=`;
        vm.GetStatusList();
        vm.userId = vm.GetUserInfo.ID;
        vm.companyId = vm.GetUserInfo.Name;
        setTimeout(() => {
            vm.FetchData();
        }, 500);
        vm.leftSearchFields = '';

    },       
    methods: {
        async GetPageSize(){            
               var vm =this;
                var params='key=DefaultPageSize'
                await DataService.GetConfigurationValueForCompanyByKey(params).then((response) => {                  
                if(response.data!=null){                                    
                    vm.PageSize = parseInt(response.data);
                }                   
            });
        },
        FetchData() {
            var vm = this;
            vm.isLoading = true;
            var params = `pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${this.SortExp}&${vm.searchCondition}`;
            DataService.Index(params).then(response => {
                if (response.data != null) {
                    if (response.data.length > 0) {
                        vm.RfidData = response.data;
                        vm.RfidData.forEach((row)=>{
                            row.isCheckBoxDisabled = false;
                            row.additionalClass = '';
                            if (row.IS_USED == '1') {
                            row.isCheckBoxDisabled = true;
                            }
                        });       
                        vm.TotalRecord = response.data[0].TOTAL;
                        vm.TotalPage = Math.ceil(vm.TotalRecord / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;
                        vm.noRecord = false;
                        vm.isLoading = false;
                    }
                    else {
                        vm.noRecord = true;
                        vm.RfidData = [];
                        vm.TotalRecord = 0
                        vm.isLoading = false;
                    }
                }
                else {
                    vm.isLoading = false;
                }
                setTimeout(function () {
                    vm.CheckBoxBootstrap();
                }, 100);
                setTimeout(function () {
                    vm.ResponsiveDataTable('tablelistingdata')
                }, 500);
            });
        },
        GetStatusList() {
            var vm = this;
            vm.isLoading = true;
            var url = `langCode=en&statusType=AssetManagement&hasglobal=true`
            DataService.GetStatusList(url).then(response => {
                const data = JSON.parse(response.data);
                vm.StatusListData = data.filter(item => item.STATUS_ID === 1001 || item.STATUS_ID === 1002);
                vm.isLoading = false;
            })
        },
        changeStatus(id, statusId) {            
            var vm = this;
            vm.confirmR(vm.$t("Are you sure you want to change the status?"), true, (vm.$t("Update") + "  " + vm.$t("Group_Item")), function () {

                var url = `TableName=${'Talygen_asset_type'}&ColumnName=${'asset_type_id'}&Id=${id}&statusId=${statusId}`
                DataService.UpdateStatus(url).then(response => {
                    if (response.data == true) {
                        vm.ShowAlert(vm.$t('Product Type  has been successfully updated.',), "success", true, vm.$t("Alert"));                        
                        vm.listheaderbuttons[2].isdisabled=true;
                        vm.listheaderbuttons[3].isdisabled=true;
                        vm.listheaderbuttons[4].isdisabled=true;
                        vm.RfidData=[];
                        vm.FetchData();
                    }
                    else {
                        vm.ShowAlert(vm.$t('An error has occurred while updating Product Type. Please retry.'), "success", true, vm.$t("Alert"));
                    }
                    vm.isLoading = false;
                })
            })
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        actionButtonClick() {

        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }

            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },

        renderActions(row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                // switch (action.title) {
                //     case 'Edit':
                //         action.isVisible = true;
                //         break;
                // }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "addProductType":
                    this.AddProductType();
                    break;
                case "ImportProductType":
                    this.ImportProductType()
                    break;
                case "Active":
                    this.Active()
                    break;
                case "Inactive":
                    this.Inactive()
                    break;
                case "DeletData":
                    this.Delete()
                    break;

            }
        },
        AddProductType: function () {
            var vm = this;
            vm.FromPage='EditProduct'
            vm.AseetTypeId = 0;
            vm.Addtype = true;
        },
        ClosePopupProductType: function () {
            var vm = this;
            vm.Addtype = false;
            vm.FromPage='';
        },
        AssetTypeEdit: function (item) {
            var vm = this;
            vm.FromPage='EditProduct'
            vm.AseetTypeId = item;
            vm.Addtype = true;
        },
        ImportProductType: function () {
            var vm = this;
            // DataService.CheckPrivilege(vm.privilegeParams + 'ImportAssetItem').then((response)=>{
            // if (response.data) {
            vm.$router.push({ path: "/Asset/ImportItemType" })
            // }else{
            //     vm.ShowAlert(vm.$t('NotAuthorizedSection'), "failure",true, "Alert");
            // }
            // });

        },
        Active: function () {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            vm.changeStatus(val, 1001)
        },
        Inactive: function () {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            vm.changeStatus(val, 1002)
        },

        Delete: function () {           
            var vm = this;
            var val = "";
            DataService.CheckPrivilege(vm.privilegeParams + 'Delete').then((response) => {
                if (response.data) {
                    $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                        if (val.length > 0)
                            val += ',';
                        val += $(this).val();
                    });
                    if (val.length > 0) {
                        vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("ASSET_CATALOGUE_ID")), function () {
                            let url = `ids=${val}&TableName=${'Talygen_asset_type'}&ColumnName=${'asset_type_id'}`;
                            DataService.DeleteProductType(url).then(response => {
                                if (response.data) {
                                    $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                                    $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');                             
                                    vm.listheaderbuttons[4].isdisabled=true;
                                   // $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                                    vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Product Type')]), "Success", true, vm.$t('Alert'));
                                    vm.FetchData();
                                }
                                else {
                                    vm.ShowAlert(vm.$t('DeletedError', [vm.$t('val')]), "failure", true, vm.$t('Alert'));
                                }
                            });
                        });
                    }
                } else {
                    vm.ShowAlert(vm.$t('NotAuthorizedSection'), "failure", true, "Alert");
                }
            });

        },
        handleDocumentClick(event) {          
            if (event.target.classList.contains('chkItems')) {
                var val='';            
                $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                    if (val.length > 0)
                    val += ',';
                    val += $(this).val();
                });
                if (val.length > 0) {                   
                    setTimeout(function () {
                        vm.listheaderbuttons[2].isdisabled=false;
                        vm.listheaderbuttons[3].isdisabled=false;
                        vm.listheaderbuttons[4].isdisabled=false;                       
                   }, 500);
                }
                else{
                    setTimeout(function () {
                        vm.listheaderbuttons[2].isdisabled=true;
                        vm.listheaderbuttons[3].isdisabled=true;
                        vm.listheaderbuttons[4].isdisabled=true;
                    }, 500);
                }
            }
        }
    }
}

</script>
<style>
.center-icon {
    display: flex;
    justify-content: center;
    align-items: center;
}
</style>